<template style>
  <div class="anno" style="width:100%;height:100%;background-color: white;">
    <div  v-for="(anno,index) in data" :key="index" class="anno_content">
      <div style="border-bottom: 1px dotted #E5E5E5;padding-bottom: 20px;">
        <div class="title text-left"><b>{{$t(anno.title)}}</b></div>
        <div class="time text-left">{{anno.time}}</div>
        <div class="content text-left" :class="anno.isUpOrDown?'contentup':'contentdown'">
          <div :ref="anno.ref">
            {{$t(anno.content)}}
          </div>
        </div>
        <div v-show="anno.isShow" v-if="anno.isUpOrDown" class="text-left"><el-button type="text" @click="anno.isUpOrDown = !anno.isUpOrDown">{{$t('announcement.pack_up')}}<i class="el-icon-arrow-up el-icon--right"></i></el-button></div>
        <div v-show="anno.isShow" v-else class="text-left"><el-button type="text" @click="anno.isUpOrDown = !anno.isUpOrDown">{{$t('announcement.read_all')}}&ensp;&ensp;→</el-button></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  // props: ['webHomeParam'],
  data () {
    return {
      data: [
        {
          title: 'announcement.title_1',
          isUpOrDown: false,
          time: '2019-05-10 09:00',
          content: 'announcement.content_1',
          isShow: false,
          ref: 'ref1'
        },
        {
          title: 'announcement.title_2',
          isUpOrDown: false,
          time: '2019-05-16 22:00',
          content: 'announcement.content_2',
          isShow: false,
          ref: 'ref1'
        }
      ]
    }
  },
  // watch: {
  //   webHomeParam: {
  //     deep: true,
  //     handler: function (newVal) {
  //       window.console.log(this.webHomeParam.isZh)
  //       if (this.webHomeParam.isZh) {
  //         this.data = this.data
  //       } else {
  //         this.data = this.data
  //       }
  //     }
  //   }
  // },
  mounted () {
    let me = this
    setTimeout(() => {
      let refs = me.$refs.ref1
      refs.forEach((i, index) => {
        if (i.offsetHeight > 145) {
          me.data[index].isShow = true
        } else {
          me.data[index].isShow = false
        }
      })
    }, 20)
  }
}
</script>
<style scoped>
.anno_content {
  width: 70%;
  margin: auto;
  padding-top: 30px;
}
.title {
  font-size: 16px;
}
.time {
  font-size: 13px;
  margin-top: 5px;
}
.content {
  font-size: 16px;
  margin-top: 1%;
}
.text-left {
  text-align: left;
}
.contentdown {
  max-height: 145px;
  overflow: hidden;
  line-height: 25px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
}
.contentup {
  height: auto;
  line-height: 25px;
}
</style>



